<template>
  <div class="hello">
    <div style="margin: 300px auto 100px; max-width: 650px; text-align: center">
      <el-form ref="form" :model="form" :rules="rules">
        <el-form-item prop="message">
          <el-input v-model="form.message" placeholder="关键字"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit('form')">查询</el-button>
        </el-form-item>
      </el-form>
    </div>

    <el-row style="margin: 20px auto;max-width: 1200px" v-if="get_movies" :gutter="20">
      <el-col style="margin-bottom: 40px;" v-for="item in movies" :key="item.id" :span="6">
        <el-card :body-style="{ padding: '0px' }">
          <div style="height: 250px;overflow: hidden"><img style="width: 100%;display: block;"
                                                           :src="item.images.large" alt=""></div>
          <div style="text-align: center">
            <p v-text="item.title" style="font-size: 16px;"></p>
            <p style="font-size: 11px;color: #6f7180">上映年份：<span v-text="item.year"></span>年</p>
            <p>
              <el-tag type="success">豆瓣评分：<span v-text="item.rating.average"></span></el-tag>
            </p>
            <p><a :href="item.alt" target="_blank">
              <el-button type="text">去豆瓣电影查看详情</el-button>
            </a></p>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    name: 'HelloWorld',
    data() {
      return {
        form: {
          message: ''
        },
        rules: {
          message: [
            {required: true, message: '请输入要查询的电影或电视剧的名称。', trigger: 'blur'}
          ]
        },
        movies: [],
        get_movies: false
      }
    },
    methods: {
      onSubmit(formName) {
        let self = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$jsonp('https://api.douban.com/v2/movie/search', {q: this.form.message}).then(json => {
              // Success.
              self.movies = json.subjects;
              self.get_movies = true;
            }).catch(err => {
              // Failed.
              return false;
            })
          } else {
            return false;
          }
        });
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



